/*defined variable*/
$colorPrimary: #222;
$colorFont: #5AC7C7;
$colorWhite: #fff;

.edit-photo {
    bottom: 100px;
    height: auto;

    .edit-photo-img {
        position: relative;
        height: 60vh;

        img {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: 200;
            margin: auto;
            width: auto;
            max-width: 100%;
            max-height: 100%;
        }
    }

    .edit-photo-content {
        padding: 15px;

        h1 {
            margin: 0 0 7px;
            font-size: 1.26em;
            color: $colorFont;
        }

        pre {
            margin: 0;
            font-size: 1.1em;
            color: $colorWhite;
            white-space: pre-wrap;
            word-wrap: break-word;
        }
    }
}

.edit-photo-bottom {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 500;
    width: 100%;
    background: $colorPrimary;

    &:after {
        content: '';
        display: block;
        clear: both;
    }

    .link {
        display: flex;
        flex-direction: column;
        float: left;
        width: 50%;
        height: 100px;

        span {
            margin-left: 0;
            margin-top: 8px;
        }
    }
}