/*defined variable*/
$colorPrimary: #222;
$colorBackground: #272730;
$colorFont: #5AC7C7;
$colorFontDark: rgb(27, 150, 150);
$colorTabbar: #999;
$colorWhite: #fff;

body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    // font-family: 'Microsoft JhengHei';
    font-size: 100%;
    vertical-align: baseline;
    color: $colorPrimary;
}

img {
    width: 100%;
    height: auto;
}

a {
    transition: all 0.4s ease;
}

/*淡出淡入特效*/
.fade-enter {
    opacity: 0;
}

.fade-enter-active {
    transition: opacity .15s ease-out;
}

.fade-leave-active {
    transition: opacity .15s ease-out;
    opacity: 0;
}

/*Welcome*/
.tutorialicon {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

#slide1 .tutorialicon {
    background-image: url('../assets/welcome/slide1.jpg');
}

#slide2 .tutorialicon {
    background-image: url('../assets/welcome/slide2.jpg');
}

#slide3 .tutorialicon {
    background-image: url('../assets/welcome/slide3.jpg');
}

.welcomescreen-text {
    bottom: 36% !important;
}

/*Welcome close*/
.tutorial-close-btn {
    padding: 5px 40px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 2.2em;
    font-style: italic;
    letter-spacing: 1px;
    color: $colorFont !important;
    border: 1px solid $colorFont;
    border-radius: 25px;

    &:hover {
        color: $colorWhite !important;
        background: $colorFontDark;
        border: 1px solid $colorFontDark;
    }
}

/*Welcome pagination*/
.swiper-pagination-bullet {
    width: 12px;
    height: 12px;
}

.welcomescreen-container .swiper-pagination-bullet.swiper-pagination-bullet-active {
    background: $colorFont !important;
}

/*navbar*/
.navbar {
    background: $colorPrimary !important;

    /*logo*/
    .logo {
        padding: 5px 0 7px;
        box-sizing: border-box;


        img {
            width: auto;
            height: 100%;
        }
    }

    /*title*/
    .page-title {
        color: $colorFont;
    }
}

/*tab*/
.tabs {
    .page-content {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
    }
}


/******device******/
.ios a,
.md a,
.md .navbar a.link {
    color: $colorFont;
}

/*page*/
.ios .page,
.md .page {
    background: $colorBackground;
}

/*toolbar*/
.ios .toolbar,
.md .toolbar {
    background: $colorWhite;
}

.ios .tabbar a,
.ios .tabbar-labels a,
.md .tabbar a.tab-link,
.md .tabbar-labels a.tab-link {
    color: $colorTabbar;
}

/*toolbar active*/
.ios .tabbar a.tab-link-active,
.ios .tabbar-labels a.tab-link-active,
.md .tabbar a.tab-link.tab-link-active,
.md .tabbar-labels a.tab-link.tab-link-active,
.md .tabbar a.tab-link.active-state,
.md .tabbar-labels a.tab-link.active-state {
    color: $colorFontDark;
}

/*ios*/
.ios .navbar .logo {
    height: 44px;
}

/*android*/
.md .navbar .title.logo {
    left: -28px;
    margin: 0 auto;
    height: 56px;
}

.md .navbar .page-title {
    left: -26px;
    margin: 0 auto;
}

.md .tabbar-labels {
    height: 53px;
}

.md .tabbar-labels a.tab-link,
.md .tabbar-labels a.link {
    padding-top: 4px;
    padding-bottom: 4px;
}

.md .navbar+.toolbar.tabbar-labels:not(.toolbar-bottom-md)~* .page-content,
.md .navbar+.toolbar.tabbar-labels:not(.toolbar-bottom-md)~.page-content {
    padding-top: 109px;
}