/*defined variable*/
$colorPrimary: #222;
$colorBackground: rgba(90, 199, 199, 0.75);
$colorPinkLight: #d10f71;
$colorWhite: #fff;
$colorShadow: rgba(0, 0, 0, 0.85);

.about {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;
    overflow: hidden;
}

.about-area {
    position: absolute;
    right: -16%;
    bottom: -18%;
    z-index: 200;
    width: 70%;
    height: 60%;
    background: $colorBackground;
    border-radius: 50%;

    .about-area-detail {
        margin: 13% 0 0 16%;
        width: 55%;

        h1 {
            margin: 0 0 20px;
            font-size: 2.6em;
            letter-spacing: 3px;
            text-align: center;
            color: $colorPrimary;
        }

        .line {
            display: block;
            margin-bottom: 20px;
            height: 2px;
            background: $colorPinkLight;
        }

        p {
            margin: 0 0 10px;
            padding: 0 5px;
            font-size: 1.7em;
            letter-spacing: 1px;
            color: $colorWhite;
            text-shadow: 1px 2px 2px $colorShadow;
        }
    }
}


@media screen and (max-width: 450px) {
    .about-area {
        right: -90px;
        bottom: -105px;
        width: 320px;
        height: 320px;

        .about-area-detail {
            margin: 12% 0 0 15%;
            width: 53%;

            h1 {
                margin: 0 0 10px;
                font-size: 1.5em;
                letter-spacing: 1px;
            }

            .line {
                margin-bottom: 10px;
            }

            p {
                margin: 0 0 5px;
                padding: 0 3px;
                font-size: 1em;
            }
        }
    }
}