/*defined variable*/
$colorPrimary: #222;
$colorFont: #5AC7C7;
$colorWhite: #fff;

.album {
    padding: 20px 15px 15px;

    .block-title {
        margin: 0 0 10px;
        font-size: 1.43em;
        line-height: 1.2;
        color: $colorFont;
    }

    .album-description {
        margin: 0 0 25px;
        font-size: 1em;
        color: $colorWhite;
    }

    .block {
        margin: 0;
        padding: 0;
    }
}

.album-item {
    margin: 0 3px 5px 0;
    width: 32% !important;
    height: 250px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    cursor: pointer;
}


@media screen and (max-width: 450px) {
    .album-item {
        height: 110px;
    }
}