/*defined variable*/
$colorPrimary: #222;
$colorFont: #5AC7C7;
$colorWhite: #fff;
$colorButton: #999;
$colorLine: #535353;

.interactive {
    .segmented {
        .button {
            height: 40px;
            font-size: 1.15em;
            font-weight: 400;
            line-height: 38px;
            letter-spacing: 1px;
            color: $colorButton;
            box-sizing: border-box;
            border: none;
            border-bottom: 1px solid $colorPrimary;
            border-radius: 0;

            &.button-active {
                background: none;
                color: $colorFont;
                border-color: $colorFont;
            }
        }
    }

    .page {
        top: 40px;
        bottom: 0;
        height: auto;
    }

    ul,
    li {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    ul {
        padding-top: 10px;
    }

    li {
        border-bottom: 1px solid $colorLine;

        &:after {
            content: '';
            display: block;
            clear: both;
        }

        .photo {
            float: left;
            width: 100px;
            height: 110px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
        }

        .interactive-area {
            display: flex;
            flex-direction: row;
            margin-left: 100px;
            padding-right: 10px;
            height: 110px;
            overflow: hidden;

            .content {
                display: flex;
                flex-direction: column;
                flex-grow: 1;
                padding: 8px 10px;
                word-break: break-all;

                h1 {
                    margin: 0 0 5px;
                    font-size: 1.1em;
                    color: $colorFont;
                }

                pre {
                    margin: 0;
                    font-size: 1.07em;
                    color: $colorWhite;
                    white-space: pre-wrap;
                    word-wrap: break-word;
                }
            }

            .buttons {
                display: flex;
                flex-direction: column;
                -webkit-align-items: center;
                align-items: center;
                -webkit-justify-content: center;
                justify-content: center;
                width: 46px;

                a {
                    width: 45px;
                    height: 45px;
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-position: center;
                }

                .show {
                    margin-bottom: 7px;

                    &.on {
                        background-image: url('../assets/interactive/on.png');
                    }

                    &.off {
                        background-image: url('../assets/interactive/off.png');
                    }
                }

                .rotate {
                    background-image: url('../assets/interactive/rotate.png');
                }
            }
        }
    }
}